@mixin transition-opacity($delay, $delayed: false) {
    transition: opacity $delay;

    @if $delayed {
        transition-delay: $delay;
    }
}

body {
    #root {
        #app:before {
            content: "";
            position: fixed;
            left: 0;
            right: 0;
            width: 100%;
            height: 100%;
            -webkit-filter: blur(3px);
            -moz-filter: blur(3px);
            -o-filter: blur(3px);
            -ms-filter: blur(3px);
            z-index: -1;
            filter: blur(3px);
            background: url(../assets/trees.png) bottom no-repeat,
                radial-gradient(circle, var(--blue) 50%, var(--white) 150%);
        }

        #app {
            #content {
                #farm {
                    .is-center {
                        display: flex;
                        align-content: center;
                        justify-content: center;
                    }
                    article {
                        font-family: "Nunito", sans-serif;
                        font-weight: 300;

                        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

                        display: flex;
                        flex-direction: column;

                        padding-top: 0;

                        .info-control:last-child {
                            margin-bottom: 0;
                        }

                        &.is-button {
                            cursor: pointer;
                        }

                        .title {
                            margin-bottom: 0.3em;

                            margin-left: -20px;
                            margin-right: -20px;

                            .tabs {
                                border-radius: 6px;
                                background-color: white;

                                .tab-title {
                                    margin-top: 3px;

                                    @media screen and (min-width: 1215px) and (max-width: 1407px) {
                                        margin-top: 2px;
                                    }

                                    @media screen and (max-width: 1215px) {
                                        margin-top: 2px;
                                    }
                                }

                                .tooltip {
                                    position: relative;
                                    display: inline-block;

                                    .tooltip-text {
                                        visibility: hidden;
                                        width: 120px;
                                        text-align: center;
                                        padding: 2px;
                                        border-radius: 2px;
                                        position: absolute;
                                        z-index: 1;
                                        left: 50%;
                                        margin-left: -60px;
                                        margin-top: 14px;
                                        background-color: #363636;
                                        border-color: transparent;
                                        color: #fff;
                                        font-size: 1em;
                                        font-weight: 500;

                                        @media screen and (min-width: 1215px) and (max-width: 1407px) {
                                            margin-top: 12px;
                                        }

                                        @media screen and (max-width: 1215px) {
                                            margin-top: 10px;
                                        }
                                    }

                                    &:hover {
                                        .tooltip-text {
                                            visibility: visible;
                                        }
                                    }
                                }

                                li {
                                    .token {
                                        margin-right: 1em;
                                        height: 35px;

                                        @media screen and (min-width: 1215px) and (max-width: 1407px) {
                                            height: 30px;
                                        }

                                        @media screen and (max-width: 1215px) {
                                            height: 25px;
                                        }
                                    }

                                    &.selected {
                                        background-color: whitesmoke;
                                        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.19);
                                    }
                                    &.disabled {
                                        a {
                                            color: lightgrey;

                                            img {
                                                opacity: 0.4;
                                                filter: alpha(opacity=40);
                                            }
                                        }
                                    }

                                    @media (pointer: coarse), (hover: none) {
                                        [title] {
                                            position: relative;
                                            display: inline-flex;
                                            justify-content: center;
                                        }
                                        [title]:focus::after {
                                            content: attr(title);
                                            position: absolute;
                                            top: 90%;
                                            color: #000;
                                            background-color: #fff;
                                            border: 1px solid;
                                            width: fit-content;
                                            padding: 3px;
                                        }
                                    }
                                }
                            }
                        }

                        .action-control {
                            width: 100%;
                            list-style: none;
                            flex-wrap: wrap;

                            align-content: center;
                            justify-content: space-evenly;

                            display: flex;
                            padding: 0;
                            margin: 0;

                            li {
                                margin: 2px;
                                flex: 1 0 48%;

                                &.action-form {
                                    .field.has-addons {
                                        display: flex;
                                        justify-content: center;

                                        .input-control {
                                            flex: 1;
                                        }
                                    }
                                }
                            }

                            .button {
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                width: 100%;
                                padding: 0.5em 1em;
                            }
                        }

                        .info-control {
                            display: flex;
                            align-content: center;
                            justify-content: flex-start;
                            flex-wrap: wrap;
                            flex-direction: row;
                            width: 100%;
                            margin: 1em 0;
                            padding: 0 2px;

                            .info-value {
                                display: -webkit-box !important;
                            }

                            h2 {
                                margin-top: 0;
                                font-size: 1rem;

                                &.info {
                                    font-size: 1.2rem;
                                    font-weight: 300;
                                    margin-right: 1em;

                                    @media screen and (min-width: 1215px) and (max-width: 1407px) {
                                        font-size: 1.1rem;
                                    }

                                    @media screen and (max-width: 1215px) {
                                        font-size: 1rem;
                                    }
                                }

                                .is-loading {
                                    background-color: transparent;
                                    margin-top: -6px;
                                }

                                &.value {
                                    font-size: 1.2rem;
                                    font-weight: 600;
                                    flex: 1;
                                    white-space: normal;

                                    .pre {
                                        margin-right: 5px;
                                    }

                                    .post {
                                        font-size: 0.7rem;
                                        margin-top: 4px;
                                    }

                                    @media screen and (min-width: 1215px) and (max-width: 1407px) {
                                        font-size: 1.1rem;

                                        .post {
                                            font-size: 0.6rem;
                                        }
                                    }

                                    @media screen and (max-width: 1215px) {
                                        font-size: 1rem;

                                        .post {
                                            font-size: 0.6rem;
                                        }
                                    }

                                    @media screen and (min-width: 380px) {
                                        .post {
                                            float: right !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
