
body #root #app #content {
    #yellow {
      .eye {
        animation: yellow-eye 10s infinite;
      }

      .eyelid {
        opacity: 0;
        animation: yellow-eyelid 10s infinite;
      }
    }

    #blue {
      .eye {
        animation: blue-eye 10s infinite;
      }

      .eyelid {
        opacity: 0;
        animation: blue-eyelid 10s infinite;
      }
    }

    #right-tree {
      transform: scale(-1,1);
      .st0{fill:#3B8349;stroke:#3B8349;stroke-width:2;stroke-miterlimit:1;}
      .st1{fill:#408E51;stroke:#408E51;stroke-width:2;stroke-miterlimit:1;}
      .st2{fill:#357841;stroke:#357841;stroke-width:2;stroke-miterlimit:1;}
      .st3{fill:#387D45;stroke:#387D45;stroke-width:2;stroke-miterlimit:1;}
      .st4{fill:#3F2D23;stroke:#3F2D23;stroke-width:2;stroke-miterlimit:1;}
      .st5{fill:#428B51;stroke:#428B51;stroke-width:2;stroke-miterlimit:1;}
      .st6{fill:#5BB772;stroke:#5BB772;stroke-width:2;stroke-miterlimit:1;}
      .st7{fill:#564231;stroke:#564231;stroke-width:2;stroke-miterlimit:1;}
      .st8{fill:#449356;stroke:#449356;stroke-width:2;stroke-miterlimit:1;}
      .st9{fill:#479658;stroke:#479658;stroke-width:2;stroke-miterlimit:1;}
      .st10{fill:#4D5D42;stroke:#4D5D42;stroke-width:2;stroke-miterlimit:1;}
      .st11{fill:#306F3A;stroke:#306F3A;stroke-width:2;stroke-miterlimit:1;}
      .st12{fill:#50B669;stroke:#50B669;stroke-width:2;stroke-miterlimit:1;}
      .st13{fill:#2B6432;stroke:#2B6432;stroke-width:2;stroke-miterlimit:1;}
      .st14{fill:#47A85C;stroke:#47A85C;stroke-width:2;stroke-miterlimit:1;}       
      .st15{fill:#4D5D42;stroke:#4D5D42;stroke-width:2;stroke-miterlimit:1;}
      .st16{fill:#479658;stroke:#479658;stroke-width:2;stroke-miterlimit:1;}       
    }

    #left-tree {
      .st0{fill:#B35549;stroke:#B35549;stroke-width:2;stroke-miterlimit:1;}
      .st1{fill:#B86051;stroke:#B86051;stroke-width:2;stroke-miterlimit:1;}
      .st2{fill:#AD4A41;stroke:#AD4A41;stroke-width:2;stroke-miterlimit:1;}
      .st3{fill:#B04F45;stroke:#B04F45;stroke-width:2;stroke-miterlimit:1;}
      .st4{fill:#3F2D23;stroke:#3F2D23;stroke-width:2;stroke-miterlimit:1;}
      .st5{fill:#BA5D51;stroke:#BA5D51;stroke-width:2;stroke-miterlimit:1;}
      .st6{fill:#D38972;stroke:#D38972;stroke-width:2;stroke-miterlimit:1;}
      .st7{fill:#564231;stroke:#564231;stroke-width:2;stroke-miterlimit:1;}
      .st8{fill:#BC6556;stroke:#BC6556;stroke-width:2;stroke-miterlimit:1;}
      .st9{fill:#BF6858;stroke:#BF6858;stroke-width:2;stroke-miterlimit:1;}
      .st10{fill:#4D5D42;stroke:#4D5D42;stroke-width:2;stroke-miterlimit:1;}
      .st11{fill:#479658;stroke:#479658;stroke-width:2;stroke-miterlimit:1;}
      .st12{fill:#C88869;stroke:#C88869;stroke-width:2;stroke-miterlimit:1;}
      .st13{fill:#A33632;stroke:#A33632;stroke-width:2;stroke-miterlimit:1;}
      .st14{fill:#C53642;stroke:#C52F42;stroke-width:2;stroke-miterlimit:1;}
      .st15{fill:#C52F42;stroke:#C52F42;stroke-width:2;stroke-miterlimit:1;}
      .st16{fill:#A8413A;stroke:#A8413A;stroke-width:2;stroke-miterlimit:1;}
    }
}

@keyframes yellow-eye {
  12.5%,
  22.5%,
  52.5%,
  67.5% {
    transform: matrix(0.866, 0.50003, 4e-5, 1, 35.355, 102.057);
  }
  15%,
  20% {
    transform: matrix(0.866, 0.50003, 4e-5, 1, 30.406, 104.915);
  }
  55%,
  65% {
    transform: matrix(0.866, 0.50003, 4e-5, 1, 42.426, 106.14);
  }
}

@keyframes yellow-eyelid {
  30%,
  35%,
  75%,
  80% {
    opacity: 0;
  }
  32.5%,
  77.5% {
    opacity: 1;
  }
}

@keyframes blue-eye {
  20%,
  35%,
  65%,
  75% {
    transform: matrix(0.866, -0.50003, -4e-5, 1, 106.065, 193.057);
  }
  22.5%,
  32.5% {
    transform: matrix(0.866, -0.50003, -4e-5, 1, 98.995, 197.14);
  }
  67.5%,
  72.5% {
    transform: matrix(0.866, -0.50003, -4e-5, 1, 111.015, 195.915);
  }
}

@keyframes blue-eyelid {
  42.5%,
  47.5%,
  82.5%,
  87.5% {
    opacity: 0;
  }
  45%,
  85% {
    opacity: 1;
  }
}
